import {useCallback} from "react";
import {suggest} from "../appRedux/actions";
import {useDispatch} from "react-redux";

const fieldsNotTagging = {
  "variants.price": 1,
  "percentSale": 1,
  "available": 1,
  "publishedAt": 1,
  // "collections": 1,
  // "vendor": 1,
  // "tags": 1,
  // "productType": 1,
  // "variants.sku": 1,
  // "product": 1,
};
const isFieldForTagging = (fieldValue) => {
  if (!fieldValue) {
    return true;
  }
  return !(fieldValue in fieldsNotTagging);
};

const isDateField = (fieldValue) => {
  return fieldValue === "publishedAt";
} ;

const DELI_RULE = "~___~";
const DELI_ATTR = "~__~";
const DELI_VALUE  = "~_~";

const buildMerchandisingRuleParams = (rules) => {
  return rules.map(rule => {
    if (isFieldForTagging(rule.field)) {
      return rule.action + DELI_ATTR + rule.field + DELI_ATTR + rule.values.join(DELI_VALUE);
    } else if (isDateField(rule.field)) {
      return rule.action + DELI_ATTR + rule.field + DELI_ATTR + rule.valueDate.fromDate + DELI_VALUE + rule.valueDate.toDate;
    } else {
      return rule.action + DELI_ATTR + rule.field + DELI_ATTR + rule.value.min + DELI_VALUE + rule.value.max;
    }
  }).join(DELI_RULE);
};

const DELI_COMMA = ",";

const buildPinnedProductParams = (ids) => {
  return ids.join(DELI_COMMA);
};

const useHandleInput = (settings) => {
  const dispatch = useDispatch();

  const handleOnInput = useCallback(e => {
    // Set query
    const q = e.target.value.trim();
    if (!q) {
      return
    }


    // Pinned products
    let pinnedProductParams = "";

    if (settings && settings.pinnedProducts) {
      if (q in settings.pinnedProducts.applyToAll) {
        pinnedProductParams = buildPinnedProductParams(settings.pinnedProducts.applyToAll[q]);
      } else if (q in settings.pinnedProducts.applyToInstantSearch) {
        pinnedProductParams = buildPinnedProductParams(settings.pinnedProducts.applyToInstantSearch[q]);
      }
    }

    // Merchandising
    let merchandisingRuleParams = [];

    if (settings && settings.merchandisingRules) {
      // Apply to all (instant search, search results)
      // If apply to all queries
      if (settings.merchandisingRules.applyToAll.all && settings.merchandisingRules.applyToAll.all.length > 0) {
        merchandisingRuleParams.push(buildMerchandisingRuleParams(settings.merchandisingRules.applyToAll.all));
      }

      // If apply to specific query
      if (q in settings.merchandisingRules.applyToAll.specific) {
        merchandisingRuleParams.push(buildMerchandisingRuleParams(settings.merchandisingRules.applyToAll.specific[q]))
      }

      // Apply to instant search
      // If apply to all queries
      if (settings.merchandisingRules.applyToInstantSearch.all && settings.merchandisingRules.applyToInstantSearch.all.length > 0) {
        merchandisingRuleParams.push(buildMerchandisingRuleParams(settings.merchandisingRules.applyToInstantSearch.all));
      }

      // If apply to specific query
      if (q in settings.merchandisingRules.applyToInstantSearch.specific) {
        merchandisingRuleParams.push(buildMerchandisingRuleParams(settings.merchandisingRules.applyToInstantSearch.specific[q]))
      }
    }

    // Synonyms
    let synonymsAnalyzer = "";
    if (settings.synonyms.analyzer !== "") {
      synonymsAnalyzer = settings.synonyms.analyzer;
    }

    // Build params
    let params = {};

    if (pinnedProductParams !== "") {
      params.pinnedProducts = pinnedProductParams;
    }

    if (merchandisingRuleParams.length > 0) {
      params.merchandisingRules = merchandisingRuleParams.join(DELI_RULE);
    }

    if (synonymsAnalyzer !== "") {
      params.synonymsAnalyzer = synonymsAnalyzer;
    }

    // Request
    if (Object.keys(params).length === 0) {
      dispatch(suggest(q));
    } else {
      dispatch(suggest(q, params));
    }
  }, [dispatch, settings]);

  const handleOnKeyDown = useCallback((e) => {
    // Redirect
    if (e.key === "Enter") {
      e.preventDefault();

      const q = e.target.value.trim();

      // If in redirect
      if (q in settings.redirect) {
        window.location = settings.redirect[q];

      // If not, redirect to normal search page
      } else {
        window.location = "/search?q=" + q
      }
    }
  }, [settings]);

  return {handleOnInput, handleOnKeyDown}
};

export default useHandleInput;
